import React from 'react';
import Footer from './Footer';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

export default function WillingnessQuestion1(props) {

	const [willingnessQuestion1, setWillingnessQuestion1] = React.useState('');
	const STATE = props.location.state
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const [loading, setLoading] = React.useState(true);

	const RadioInputOptions = [
		{ key: '525', name: '525' },
		{ key: '550', name: '550' },
		{ key: '600', name: '600' },
		{ key: '650', name: '650' },
		{ key: '750', name: '750' },
	]

	React.useEffect(async ()=>{
		await db.get(db.model.riskProfile).then(p=>{
			if(p){
				setWillingnessQuestion1(p.willingnessQuestion1 || "")
			} else {
				setWillingnessQuestion1("")
			}
		})
		setLoading(false)
	},[])

	const checkValidation = () => {
		if (willingnessQuestion1) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [willingnessQuestion1])

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={72} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="header1dark">
								A coin flipping game has the following rules
								<ul>
									<li>If you get heads, you will win Rs. 1,000</li>
									<li>If you get tails, you will win Rs. 500</li>
								</ul>
								What is the maximum entry fee you are willing to play the game?
							</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row',marginTop:"6%" }}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setWillingnessQuestion1(val.target.value)
											}}
											className="bodyTextDark"
											checked={willingnessQuestion1 === obj.key}
										/>
										<label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/risk-profile/8"
				state={{...STATE,willingnessQuestion1}}
				saveData={{ willingnessQuestion1 }}
				buttonText="Confirm"
				nextDisabled={nextDisabled}
				/>
		</div>
	)
}
